.exf-container {
    &.exf-theme-fluent {
        font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

        .exf-cnt {
            border: 0;

            > legend {
                font-size: 1.2rem;
                font-weight: 600;
            }
        }

        .exf-ctl-cnt {
            border: 1px solid transparent;
            appearance: none;
            position: relative;
            min-height: 2.525rem;
            height: auto;
            padding: 2rem 0.5rem 0.5rem;
            line-height: 1;

            .exf-ctl-cnt {
                // nested controls
                padding-left: 0;
                margin: 0;
            }

            > .exf-ctl {
                 [name] {
                    outline: none;
                    width: 100%;
                }

                 input,
                 textarea,
                 select {
                    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
                    //appearance: none;
                    border: 1px solid var(--field-box-shadow-subtle);
                    height: 2rem;
                    padding: 0.3125rem;
                    z-index: 0;
                    //width: auto;

                    ::placeholder {
                        opacity: 0.6;
                    }
                }

                textarea {
                    width: 100%;
                    min-height: 6em;
                }

                select {
                    min-width: 15rem;
                }

                input[type="color"],
                [type="date"],
                [type="time"],
                [type="datetime"],
                [type="datetime-local"] {
                    min-width: 3rem;
                }

                &.exf-nmbr-btns {
                    .exf-input {
                        > .nmbr-m,
                        > .nmbr-p {
                            height: 2.7rem;
                            width: 2rem;
                            padding: 0.3125rem;
                            border: 1px solid var(--field-box-shadow-subtle);
                        }
                    }
                }

                 input:focus,
                 textarea:focus,
                 select:focus,
                 input:hover,
                 textarea:hover,
                 select:hover,
                 .exf-btn:hover {
                    border-color: var(--field-box-shadow);
                }

                 input:disabled,
                 textarea:disabled,
                 select:disabled {
                    background-color: var(--color-disabled);
                }

                 input:read-only,
                 textarea:read-only,
                 select:read-only {
                    background-color: var(--color-readonly);
                }

                > label {
                    position: absolute;
                    top: 0.5rem;
                    left: 0;
                }

                [type="radio"],
                [type="checkbox"] {
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    appearance: none;
                    outline: none;
                    width: 20px;
                    height: 20px;
                    border: solid 1px #cccccc;
                    margin-right: 8px;
                    position: relative;

                    &:checked::before,
                    &:checked::before {
                        content: "";
                        width: 14px;
                        height: 14px;
                        background-color: var(--color-button);
                        position: absolute;
                        top: 2px;
                        left: 2px;
                    }

                    &:checked::before {
                        border-radius: 100%;
                    }
                }

                [type="radio"] {
                    border-radius: 100%;
                }

                select {
                    outline: 0;
                    box-shadow: none;
                    background-color: var(--color-background);
                    border: 1px solid var(--field-box-shadow-subtle);

                    background-image: none;
                    flex: 1;
                    height: 3rem;
                    padding: 0.3125rem;
                    //color: #fff;
                    cursor: pointer;

                    /* Remove IE arrow */
                    &::-ms-expand {
                        display: none;
                    }
                }

                &.exf-rng-output {
                    output {
                        padding: 0.375rem;
                        border: 1px solid var(--color-shade);
                        border-radius: 1em;
                        background-color: var(--color-background-variation);
                        margin-left: 1em;
                        margin-top: -1.7em;
                    }
                }

                
            }
        }

        .exf-help-wrapper {
            font-size: smaller;
            .exf-help {
                &.exf-help-invalid {
                    border-color: #cc3344;
                }
            }
        }

        .exf-input-group {
            label {
                > .exf-caption {
                    display: inline-block;
                    height: auto;
                    vertical-align: text-bottom;
                    padding: 0.2rem 0.4rem 0.2rem 0.1rem;
                    margin-right: 1rem;

                    .exf-caption-description {
                        font-size: 0.9rem;
                    }

                    .exf-caption-main {
                        margin-bottom: 0.4em;
                        margin-top: -0.4em;
                        font-weight: bold;
                    }
                }
            }
        }

        .exf-btn {
            -webkit-appearance: button;
            cursor: pointer;
            padding: 1rem 2rem;
            //height: 2.125rem;
            text-align: center;
            background-color: var(--color-button);
            border: 1px var(--color-shade) solid;
            color: var(--color-buttontext);
            display: inline-block;
            outline: none;
            font-size: 0.875rem;
            margin: 0.15625rem 0;
            position: relative;
            vertical-align: middle;

            &:active {
                background: #eeeeee;
                color: #262626;
                box-shadow: none;
            }

            &:hover:not(:disabled) {
                border-color: #787878;
            }

            &.primary {
                background-color: var(--color-button-primary);
            }

            &:disabled{
                background-color: var(--color-button-disabled);
            }
        }
    }
}
